@font-face {
    font-family: "Retrock";
    src: url('../Styles/fonts/Retrock.eot');
    /* IE9 Compat Modes */
    src: url('../Styles/fonts/Retrock.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */
    url('../Styles/fonts/Retrock.woff2') format('woff2'), /* Super Modern Browsers */
    url('../Styles/fonts/Retrock.woff') format('woff'), /* Pretty Modern Browsers */
    url('../Styles/fonts/Retrock.ttf') format('truetype'), /* Safari, Android, iOS */
    url('../Styles/fonts/Retrock.svg#svgFontName') format('svg');
    /* Legacy iOS */
}